.list{
    color: #fff;
    width: 300px;
    background-color: sandybrown;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}
.list p{
    padding: 10px;
    position: relative;
}
.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}
.faicon{
margin: 0 5px;
font-size: 15px;
}